.navbar-items {
  background-color: black;
  height: 70px;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1000;
  transition: all 0.3s ease;
}
.navbar-items.scrolled {
  box-shadow: 0px 0px 10px rgb(93, 224, 192);
  transition: all 0.3s ease;
}
.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style-type: none;
  text-align: center;
  width: 90%;
  -webkit-justify-content: end;
          justify-content: end;
}
.nav-links {
  color: whitesmoke;
  text-decoration: none;
  padding: 0.5rem 1rem;
}
a.nav-links {
  position: relative;
}
a.nav-links::before {
  content: "";
  position: absolute;
  width: 85%;
  height: 2px;
  bottom: 0;
  left: 9%;
  background-color: #fff;
  visibility: hidden;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  transition: all 0.3s ease-in-out;
}
a.nav-links:hover::before {
  visibility: visible;
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}
.menu-icon {
  display: none;
}
.navigation-icon {
  display: inline-block;
  margin-right: 10px;
  font-size: 1.3rem;
  position: relative;
  top: 3px;
}
.social-icons {
  display: none;
}
.text-color-logo {
  color: transparent;
  background: linear-gradient(to right, #12c2e9, #c471ed, #f64f59);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-animation: rainbow 10s infinite linear;
          animation: rainbow 10s infinite linear;
  font-weight: 500;
}
.text-color-logo span {
  font-size: 1.9rem;
  font-weight: 450;
}

@-webkit-keyframes rainbow {
  from {
    -webkit-filter: hue-rotate(0deg);
            filter: hue-rotate(0deg);
  }
  to {
    -webkit-filter: hue-rotate(-360deg);
            filter: hue-rotate(-360deg);
  }
}

@keyframes rainbow {
  from {
    -webkit-filter: hue-rotate(0deg);
            filter: hue-rotate(0deg);
  }
  to {
    -webkit-filter: hue-rotate(-360deg);
            filter: hue-rotate(-360deg);
  }
}

@media screen and (max-width: 870px) {
  .navbar-items {
    width: 104%;
  }
  .nav-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    height: 26rem;
    position: absolute;
    left: -100%;
    top: 70px;
    opacity: 1;
    transition: all 0.3s ease;
  }

  .nav-menu.active {
    background: #141414;
    left: 0;
    opacity: 1;
    transition: all 0.3s ease;
  }
  .nav-links {
    text-align: center;
    padding: 0.9rem;
    width: 100%;
    display: table;
  }
  .nav-links:hover {
    border-radius: 0px;
  }
  a.nav-links::before {
    width: 40%;
    left: 31.5%;
  }
  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: white;
  }
  .social-icons {
    display: -webkit-flex;
    display: flex;
    position: fixed;
    top: 24rem;
    z-index: 1000;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    left: -100%;
    transition: left 0.3s ease;
    /* border: 1px solid yellow; */
    width: 100%;
  }
  .social-icons.active {
    left: 0%;
    transition: left 0.3s ease;
  }
  .social-icons div {
    font-size: 2rem;
    text-decoration: none;
  }
  .social-icons div:hover {
    color: rgb(10, 194, 102);
  }
  .social-icons .facebook,
  .linkedin,
  .github,
  .instagram,
  .twitter {
    color: white;
  }
  .github {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  .text-color-logo {
    margin-left: -75%;
  }
}
@media screen and (min-width: 430px) and (max-width: 870px) {
  .menu-icon {
    right: 1rem;
  }
}

